.footer {
    width: 100%;
    background-color: #000;
    color: #eee;
}

.footer .container {
    max-width: 1240px;
    margin: auto;
    text-align: center;
}

.footer ul {
    display: flex;
    justify-content: center;
}

.footer li {
    padding: 1rem;
}

.footer li a {
    color: #fff;
}

.footer li a:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid var(--secondary-color);
}

.bottom {
    text-align: center;
}

.logo-footer {
    width: 150px;
    cursor: pointer;
}

.line {
    margin-bottom: 1rem;
}

@media screen and (max-width: 940px) {
    .footer li {
        padding: 0.5rem;
    }

    .footer li a {
        font-size: 14px;
    }

    .logo-footer{
        width: 100px;
    }
}