.about {
    background-color: #f8f8f8;
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    width: 100%;
    position: relative;
}


.about .container {
    width: 1240px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.about .daniela-jpg{
    width: 600px;
    height: 600px;
    width: 100%;
}

.about h2 {
    font-size: 3rem;
}

.about .col-2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
}

.about .col-2 p:first-of-type {
    margin: 2rem 0;
}

.about .col-2 p:nth-child(4) {
    font-size: 1.8rem;
    font-style: italic;
    font-weight: 600;
} 

.about button {
    margin-top: 1rem;
}




 @media screen and (max-width:940px) {
    .about{
        padding-top: 1rem;
    }

    .about .container {
        grid-template-columns: 1fr;
    }

    .about .container {
        width: 100%;
    }

    .about .container img {
        padding: 0rem;
        height: 300px;
    }

    .about .container .col-2 {
        padding: 0;
    }

    .about button {
        margin-bottom: 1rem;
        width: 180px;
        font-size: 12px;
    }

    .about h2 {
        font-size: 2.2rem;
        padding-top: 20px;
    }

    .about .col-2 p:first-of-type{
        margin: 1rem 0;
        font-size: 16px;
    }

    .about .col-2 p:nth-child(4){
        font-size: 20px;
    }
} 